/* Container & content */
.history-content {
  flex: 1;
  padding: 20px;
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  color: inherit;
}

.history-content h1 {
  font-family: "New Rocker", system-ui;
  font-weight: 400;
  font-style: normal;
  text-align: center;
  margin-bottom: 20px;
}

.empty-msg {
  text-align: center;
  opacity: 0.7;
  font-style: italic;
}


/* History list */
.history-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 100%;
}

/* Card styling */
.history-card {
  background: rgba(40, 40, 40, 0.3);
  backdrop-filter: blur(6px);
  border-radius: 12px;
  padding: 12px 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
  position: relative;
  width: 100%;
  max-width: 900px; /* match HowToUse width */
  margin: 0 auto;   /* center card within list */
  box-sizing: border-box;
}

/* Light mode */
.light .history-card {
  background: rgba(255, 255, 255, 0.3);
  border: 1px solid rgba(46, 46, 46, 0.1);
  color: #000;
}

/* Header of each card */
.history-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  width: 100%;
  gap: 10px;
}

.history-header span {
  display: block;
}

.saved-time {
  font-size: 11px;
  color: #ccc;
  margin-top: 2px;
}

.light .saved-time {
  color: rgb(146, 146, 146);
}

/* Buttons inside cards */
.history-buttons button {
  border: none;
  color: white;
  padding: 4px 10px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 12px;
  transition: 0.2s;
}

/* Snapshot details */
.snapshot-details {

  font-size: 12px;
  animation: fadeIn 0.2s ease-in-out;
}


.snapshot-details .leaderboard {
  width: 100%;
  max-width: 100%;
}

.snapshot-details th,
.snapshot-details td {
  padding: 6px;
  text-align: left;
  white-space: normal;    /* allow wrapping */
  word-break: break-word; /* break long words */
  overflow-wrap: anywhere;
  font-size: 15px;
}

/* tighter layout on very small screens */
@media (max-width: 600px) {
  .snapshot-details th,
  .snapshot-details td {
    padding: 5px;
    font-size: 11px;
  }
}

/* Smooth fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-5px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Clear All button */
.clear-all-btn {
  margin-bottom: 15px;
  background: #527230;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 6px 14px;
  cursor: pointer;
  transition: 0.2s;
  font-size: 13px;
  display: block;
  margin-left: auto;
}

.clear-all-btn:hover {
  background: #74a143;
}

.delete-btn {
  background: #d9534f;
}

.delete-btn:hover {
  background: #ff706b;
}

/* Background image like other pages */
.app-container {
  background-image: url("../images/huns.jpg");
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Responsive tweaks */
@media (max-width: 700px) {
  .snapshot-details table {
    font-size: 12px;
    min-width: unset;
  }

  .history-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }

  .clear-all-btn {
    font-size: 12px;
    padding: 5px 10px;
  }

  .history-card {
    min-width: unset;
  }
}
